<script>
import StatusIcon from '@/components/StatusIcon.vue';
import { change_app_config_base, del_app, get_app_info } from '@/scripts/apps/product';
import _ from 'lodash';

export default {
    data() {
        return {
            hidden: true,
            app_id: '',
            data: {
                app_id: '',
                app_secret: '',
            },
            temp: {
                auto_renew_status: true,
                auto_renew_type: 0
            },
            firstRefresh: true,
        }
    },
    methods: {
        async operation(type, eventName) {
            
            this.$swal({
                icon: "warning",
                title: "操作确认",
                text: `确认执行 ${eventName} 操作?`,
                showCancelButton: true,
                confirmButtonText: "确认",
                cancelButtonText: "取消",
            }).then(async (res) => {
                if (!res.isConfirmed) {
                    return false;
                }
                const loading = this.$vs.loading({
                    target: this.$refs.card,
                    text: '操作中...'
                })

                let resp = null;
                if (type === 'delete') {
                    resp = await del_app(this.app_id);
                } else if (type === 'change_base') {
                    resp = await change_app_config_base(this.app_id, {
                        app_name: this.temp.app_name,
                        auto_renew_status: this.temp.auto_renew_status ? 1 : 0,
                        auto_renew_type: parseInt(this.temp.auto_renew_type)
                    });
                }else if (type === 'reset') {
                    resp = {
                        code: 0,
                        msg: '暂不支持'
                    }
                }

                loading.close();

                if (resp === null) {
                    return;
                }

                if (resp.code !== 1) {
                    this.$swal({
                        icon: "error",
                        title: "操作失败",
                        text: resp.msg,
                        confirmButtonText: "返回"
                    })
                } else if (resp.code === 1) {
                    this.$swal({
                        icon: "success",
                        title: "操作成功",
                        text: resp.msg,
                        confirmButtonText: resp.back ? "返回" : "确定"
                    }).then(() => {
                        if (resp.back) {
                            this.$router.push({path: resp.back});
                        }
                    })
                }
                
            })
        },
        async refreshData() {
            const firstRefresh = this.firstRefresh;
            this.firstRefresh = false;

            const loading = this.$vs.loading({
                target: this.$refs.card,
            })
            const resp = await get_app_info(this.app_id);
            if (resp.code !== 1) {
                if (resp.code === -1 || resp.code === 403) {
                    this.$swal({
                        icon: "error",
                        title: "错误",
                        text: "应用不存在或无权限",
                        confirmButtonText: "返回"
                    }).then(() => {
                        this.$router.back();
                    })
                    return;
                }
                this.$swal({
                    icon: "error",
                    title: "错误",
                    text: resp.msg,
                    confirmButtonText: "返回"
                }).then(() => {
                    this.$router.back();
                })
            }

            this.data = resp.data;
            resp.data.auto_renew_status = !!resp.data.auto_renew_status;

            if (firstRefresh || true) this.temp = _.cloneDeep(resp.data);
            loading.close();
        },
        copy(text) {
            navigator.clipboard.writeText(text);
            this.$swal({
                icon: "success",
                title: "成功",
                text: "复制成功",
                confirmButtonText: "OK"
            })
        }
    },
    async mounted() {
        this.app_id = this.$route.params.app_id;
        if (!this.app_id) {
            this.$swal({
                icon: "error",
                title: "错误",
                text: "错误的跳转",
                confirmButtonText: "返回"
            }).then(() => {
                this.$router.back();
            })
        }
        this.refreshData();
    },
    components: {
        StatusIcon
    }
}
</script>

